@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body{ 
  margin: 0;
  background-image:  url(/App_Weather/static/media/weather.90e24d62.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}
.App{width:700px;margin-top:30vh;margin-left:auto;margin-right:auto;font-family:"Roboto",sans-serif}@media(orientation: portrait)and (max-width: 1024px){.App{width:80%;margin-top:20%}}@media(orientation: landscape)and (max-width: 1366px){.App{margin-top:10%;width:80%}}@media(orientation: landscape)and (max-width: 823px){.App{margin-top:5%}}
input{display:block;margin:0 auto;padding:5px;width:80%;font-size:40px;text-transform:uppercase;font-weight:bold;background-color:rgba(49,125,211,.4);border-radius:3em}@media(orientation: portrait)and (max-width: 414px){input{font-size:30px}}@media(orientation: portrait)and (max-width: 375px){input{font-size:25px}}@media(orientation: landscape)and (max-width: 823px){input{font-size:25px}}@media(orientation: landscape)and (max-width: 568px){input{font-size:20px}}
.result{width:100%;margin-top:2%;border-radius:3em;font-size:30px;text-align:center}.result div{opacity:1;text-align:left;padding:5%;border-radius:2em;background-color:rgba(255,255,255,.4)}.result div h1{font-size:35px}.result div h2{font-size:30px}@media(orientation: portrait)and (max-width: 414px){.result div h1{font-size:30px}.result div h2{font-size:25px}}@media(orientation: portrait)and (max-width: 375px){.result{font-size:25px}.result div{border-radius:1em}.result div h1{font-size:27px}.result div h2{font-size:23px}}@media(orientation: portrait)and (max-width: 320px){.result div h1{font-size:23px}.result div h2{font-size:18px}}@media(orientation: landscape)and (max-width: 823px){.result{font-size:30px}.result div{border-radius:.5em;padding:1% 2% 2% 2%}.result div h1{margin:0;font-size:20px}.result div h2{font-size:15px}}@media(orientation: landscape)and (max-width: 568px){.result{font-size:25px}.result div h1{font-size:18px}.result div h2{font-size:13px}}
